.app-wrapper {
  position: absolute;
  top: 30px;
  left: 50%;
  transform: translate(-50%, 0);
  min-width: 600px;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-right: 39px;
  min-width: 835px;
}

h1 {
  color: rgb(82, 82, 82);
  display: inline-block;
}

p {
  width: 500px;
  margin: 0 auto;
  color: rgb(54, 54, 54);
  margin-top: 50px;
}

.about p {
  display: inline-block;
  margin: 5%;
  width: 80%;
}

@media only screen and (max-width: 800px) {
  .app-wrapper {
    margin-left: 2%;
    margin-right: 2%;
    left: 0;
    transform: translate(0, 0);
    min-width: 0;
  }

  .header {
    min-width: 0;
  }
}
