.new-note-form__card {
  margin-bottom: 50px;
}

.new-note-form input {
  min-width: 600px;
  border: none;
  background-color: white;
  border-bottom: 1px solid gray;
}

.new-note-form__card {
  padding: 50px 40px;
  min-width: 835px;
}

.new-note-form {
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 800px) {
  .new-note-form__card {
    min-width: 0;
    width: 100%;
  }

  .new-note-form input {
    min-width: 0;
    width: 70%;
  }
}
