.note-container {
  padding: 10px;
  width: fit-content;
  background-color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-width: 776px;
  border-bottom: 1px solid gray;
}

.text-container {
  max-width: 500px;
}

.button-group {
  display: flex;
  flex-direction: row;
  column-gap: 3px;
}

@media only screen and (max-width: 800px) {
  .button-group {
    flex-direction: column;
    row-gap: 10px;
  }

  .note-container {
    width: 100%;
    min-width: 0;
    margin: 0;
  }

  .text-container {
    max-width: none;
  }
}
